.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}
.footer{
  background-color: #333; /* Footer arka plan rengi */
  color: #fff; /* Yazı rengi */
  padding: 20px; /* İçerikten kenarlara iç dolgu (padding) */
  text-align: center; /* Yazıların ortalanması */
  height: 100px; /* Footer yüksekliği */
  width: 100%; /* Footer genişliği (sayfanın tamamını kaplaması için) */
}