@tailwind base;
@tailwind components;
@tailwind utilities;

body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
.satisfy-regular {
  font-family: "Satisfy", cursive;
  font-weight: 400;
  font-style: normal;
  color: #ce2829;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}
.footer{
  background-color: #333; /* Footer arka plan rengi */
  color: #fff; /* Yazı rengi */
  padding: 20px; /* İçerikten kenarlara iç dolgu (padding) */
  text-align: center; /* Yazıların ortalanması */
  height: 100px; /* Footer yüksekliği */
  width: 100%; /* Footer genişliği (sayfanın tamamını kaplaması için) */
}
h1,
h2,
p {
  font-family: "Barlow", sans-serif;
}

.main-container {
  display: flex;
  flex-direction: column;
  width: 30%;
  margin: 1% auto;
}

.form-container {
  display: flex;
}

.product-inches {
  display: inline-flex;
  flex-direction: column;
  grid-gap: 1rem;
  gap: 1rem;
}

.dough-size {
  display: flex;
  flex-direction: column;
  grid-gap: 1rem;
  gap: 1rem;
  margin-left: 10rem;
}

.dough-size select {
  width: 10rem;
  height: 1.5rem;
  font-size: 1rem;
}

.ek-malzemeler {
  display: flex;
  flex-direction: column;
  margin-top: 3rem;
}

.malzeme-selection {
  display: flex;
  flex-wrap: wrap;
  grid-gap: 1rem;
  gap: 1rem;
  margin-top: 5%;
  width: 100%;
  font-weight: bold;
  color: grey;
  font-size: 0.9rem;
}

.malzeme {
  width: 10rem;
}

div #malzeme {
  margin: 0 0.1rem;
}

div .size-margin {
  margin: 0 0.5rem;
}

.order-notes {
  display: flex;
  flex-direction: column;
  margin-top: 3rem;
  grid-gap: 1rem;
  gap: 1rem;
}

.order-notes h3 {
  width: 100%;
}

.order-notes input {
  padding: 1rem;
  border: 0.1rem solid grey;
  border-radius: 0.5rem;
}

.span-color span {
  color: red;
}

.cizgi {
  border-bottom: 0.1rem solid grey;
  margin-top: 2rem;
}

.common {
  display: flex;
  justify-content: space-between;
  margin-right: 1rem;
}

.counters {
  display: inline-flex;
  margin-left: 2.4rem;
}

.counters .inc,
.dec {
  height: 3rem;
  width: 2.2rem;
  border-radius: 0.2rem;
  background-color: #fdc913;
  border: none;
  cursor: pointer;
}

.counter {
  margin-top: 2rem;
}

.counters .but {
  display: flex;
  align-items: center;
  justify-content: center;
  border: 1px solid grey;
}

.column {
  flex-direction: column;
}

.buton-bet {
  justify-content: space-between;
}

.border {
  border: 1px solid rgb(189, 183, 183);
  width: 51%;
  padding: 2rem 0 2rem 2rem;
}
.touch {
  height: 3rem;
  width: 3rem;
  border: solid 1px #fdc913;
  text-align: center;
}

.order-button {
  float: right;
  width: 60%;
  padding: 1rem;
  font-size: 1rem;
  background-color: #fdc913;
  border: none;
  border-radius: 0.4rem;
}

.total span {
  color: red;
  font-weight: bold;
}

.total p {
  font-weight: bold;
}

.total-screen {
  border-radius: 5px;
}

.pızzaa {
  /* Resmi iki katı yükseklikte ayarla */
  margin-top: -205px; /* Resmi yukarı doğru kaydır ve alt yarısını göster */
  width: 350px;
  -webkit-clip-path: inset(54% 0 0 0);
          clip-path: inset(54% 0 0 0);
}

